import axios from 'axios'
import {
  matchedListToApi,
  apiToMessageThread,
  exceptionListToApi,
  apiToRecordList,
  apiToMatchedList,
  autoMatchListToApi,
  manualMatchListToApi,
} from './converters/reconciliationConverters'

export default {

  getExceptionsRecords(payload, selectedCompany) {
    const data = exceptionListToApi(payload, selectedCompany)
    const url = ['/ReportTransactions/Companies/',
      data.companyId,
      '/RefCompanies/',
      data.refCompanyId,
      '/StatementTypes/',
      data.statementTypeId,
      '/StartDate/',
      data.startDate,
      '/EndDate/',
      data.endDate,
      data.queryString,
    ].join('')
    return axios.get(url)
      .then(data => data.map(apiToRecordList))
  },

  getMatchedSets(payload, selectedCompany) {
    const data = matchedListToApi(payload, selectedCompany)
    const url = ['/ReportTransactions/Companies/',
      data.companyId,
      '/RefCompanies/',
      data.refCompanyId,
      '/StatementTypes/',
      data.statementTypeId,
      '/StartDate/',
      data.startDate,
      '/EndDate/',
      data.endDate,
      data.queryString,
    ].join('')
    return axios.get(url)
      .then(apiToMatchedList)
  },

  getSRMessages(payload) {
    return axios.get('/Conversations/Reconciliations/' + payload)
      .then(apiToMessageThread)
  },

  sendSRMessage(payload) {
    const postData = {
      body: payload.message,
    }
    return axios.post('/Conversations/Reconciliations/' + payload.recId + '/Messages', postData)
      .then()
  },

  deleteMessage(payload) {
    return axios.delete('/Conversations/Messages/' + payload.id)
      .then()
  },

  editMessage(payload) {
    console.log(payload)
    const patchData = {
      body: payload.body,
    }
    return axios.patch('/Conversations/Messages/' + payload.id, patchData)
      .then()
  },

  matchAuto(payload, selectedCompany) {
    const postData = autoMatchListToApi(payload, selectedCompany)
    let postString = '/MatchingSets/AutoMatching/Companies/' + postData.companyId + '/RefCompanies/' + postData.refCompanyId
    postString += '/StatementTypes/' + postData.statementTypeId + '/StartDate/' + postData.startDate + '/EndDate/'
    postString += postData.endDate + postData.queryString
    return axios.post(postString)
      .then()
  },

  manualMatch(payload, selectedCompany) {
    const postData = manualMatchListToApi(payload, selectedCompany)
    const postString = '/MatchingSets/ManualMatching/Companies/' + postData.companyId + '/StatementTypes/' + postData.statementTypeId + postData.queryString
    return axios.post(postString, postData.payload)
      .then()
  },

  unmatchSet(payload) {
    return axios.delete(/MatchingSets/ + payload)
      .then()
  },

}
