<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.num"
           @edit="onEdit">

    <CountrySelect
      v-model="cpModel.country"
      :disabled="!editMode"
      componentClass="cp-row"
    />

    <div class="cp-row">
      <label>{{ (cpModel.countryId === 1) ? $t('VAT ID') : $t('Sales Tax ID') }}:</label>
      <input
        type="text"
        class="form-control"
        v-model="cpModel.num"
        name="num"

        v-validate="'required'"
        :class="[{ 'is-invalid': errors.has('num')}]"
        :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label>{{$t('Reporting Regularity')}}:</label>
      <RecurringDateSelect
        v-model="cpModel.reportingRecurringDate"
        name="repReg"
        v-validate="'required'"
        :class="[{'form-control': true, 'is-invalid': errors.has('repReg'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateSelect>
    </div>

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'
import Multiselect from 'vue-multiselect'
import RecurringDateSelect from '@/components-app/selects/RecurringDateSelect'
import CountrySelect from '@/components-app/selects/CountrySelect'

const vatVueConfig = {
  components: {
    Multiselect,
    RecurringDateSelect,
    CountrySelect,
  },
  props: {
    cProp: {
      required: false,
      default() {
        return {}
      },
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: !this.cProp.num,
    }
  },
  methods: {
    save() {
      var self = this
      const action = self.cProp.num ? 'updateSCVat' : 'createSCVat'
      self.loading = true
      return self.$store.dispatch(action, self.cpModel)
        .then(() => {
          self.$emit('success')
          if (action === 'createSCVat') {
            self.cpModel = {}
          }
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCVat', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
  },
  watch: {
    cProp: {
      handler(value) {
        this.cpModel = Object.assign({}, value)
        this.loading = false
      },
      deep: true,
    },
  },
}

export default merge({}, basePropMixin, vatVueConfig)
</script>
