<template>
  <input type="text" class="form-control form-control-sm" @input="setFilter">
</template>


<script>
import {memoizedLookupFunction, debounce} from '@/helpers/functions'

var vueComponentOptions = {}

var vueComponentFn = function(store) {
  vueComponentOptions.props = {
    filterKey: {
      type: String,
      required: true,
    },
    compare: {
      type: Function,
      required: true,
    },
  }

  vueComponentOptions.methods = {
    setFilter: debounce(function(event) {
      var self = this
      var value = event.target.value

      if (value !== null && value !== '') {
        var comparatorFn = (function(fValue) {
          var derefFuncs = {}
          return function(item) {
            return self.compare(memoizedLookupFunction(item, self.filterKey, derefFuncs), fValue)
          }
        })(value)

        const payload = {
          key: self.filterKey,
          comparator: comparatorFn,
        }
        store.mutations.addFilter(payload)
      } else {
        store.mutations.removeFilter(self.filterKey)
      }
    }, 300),
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn
</script>
