  <div class="content-section">

    <div class="left-side">

      <div class="left-side-company-info">
        <CompanyCard
          :company="company"
          size="m"
          extend="true"
        ></CompanyCard>
      </div>

      <nav class="nav flex-column nav-pills">
        <router-link :to="'/companies/' + id + '/upload'" class="nav-link">
          <i class="fas fa-cloud-upload-alt"></i>
          <span class="nav-link-text">{{$t('Upload')}}</span>
        </router-link>
        <router-link :to="'/companies/' + id + '/contacts'" class="nav-link">
          <i class="far fa-address-book"></i>
          <div class="nav-link-text">{{$t('Contacts')}}</div>
        </router-link>
        <router-link :to="'/companies/' + id + '/profile'" class="nav-link">
          <i class="fas fa-user"></i>
          <div class="nav-link-text">{{$t('Profile')}}</div>
        </router-link>
        <router-link :to="'/companies/' + id + '/collaborators'" class="nav-link">
          <i class="fas fa-users"></i>
          <div class="nav-link-text">{{$t('Collaborators')}}</div>
        </router-link>
        <router-link :to="'/companies/' + id + '/records'" class="nav-link">
          <i class="fas fa-save"></i>
          <div class="nav-link-text">{{$t('Records')}}</div>
        </router-link>
        <router-link :to="'/companies/' + id + '/fx'" class="nav-link">
          <i class="fas fa-chart-line"></i>
          <div class="nav-link-text">{{$t('Fx Rates')}}</div>
        </router-link>
        <router-link :to="'/companies/' + id + '/reconciliations'" class="nav-link">
          <i class="fas fa-exchange-alt fa-rotate-90"></i>
          <div class="nav-link-text">{{$t('Reconciliations')}}</div>
        </router-link>
      </nav>
    </div>

    <div class="right-side">
      <router-view></router-view>
    </div>

    <loading :loading="loading"></loading>
  </div>
</template>

<script>
import CompanyCard from '@/components-app/CompanyCard'
export default {
  components: {CompanyCard},
  props: ['id'],
  created() {
    this.loading = true
    this.$store.dispatch('loadSelCompany', this.id)
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    company() {
      return this.$store.getters.selectedCompany || {}
    },
  },
}
</script>
