import tableTools from '@/components-lib/table-tools'
import CompanyCard from '@/components-app/CompanyCard'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'
import importFPMMixin from '../../UploadPage/importFPMMixin'

// TODO: upload()
// TODO: reconcile()
// TODO: addAlias()

var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'ContactsTable'

  vueComponentOptions.mixins = [importFPMMixin, tableStoreMixin(tableStore)]

  vueComponentOptions.directives = {
    Sort: tableTools.SortDirective(tableStore),
  }

  vueComponentOptions.components = {
    CompanyCard,
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
    Pagination: tableTools.Pagination,
  }

  vueComponentOptions.data = function() {
    return {
      loading: false,
      itemsPerPage: 5,
      showFilters: false,
      compare: {
        string: comparators.string,
        numAsString: comparators.numAsString,
        number: comparators.number,
        day: comparators.day,
      },
      newAlias: '',
      newAliasState: null,
      newAliasCompany: null,
    }
  }

  vueComponentOptions.computed = {
    contacts() {
      return this.$store.getters.contacts
    },
  }

  vueComponentOptions.mounted = function() {
    tableStore.commit('setItems', this.$store.getters.contacts)
  }

  vueComponentOptions.methods = {
    load(newSet) {
      this.contractorName = newSet.company.name
    },
    emitEvent(event) {
      this.$emit(event)
    },
    toggleFilters: function() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    startReconciliation(companyId) {
      this.$router.push({ name: 'Reconciliations', params: { startReconciliationFor: companyId }})
    },
    showAddAliasModal(company) {
      this.newAliasCompany = company
      console.log(this.newAliasCompany)
      this.$bvModal.show('add-alias-modal')
    },
    checkFormValidity() {
      const valid = this.$refs.addAliasForm.checkValidity()
      this.newAliasState = valid
      return valid
    },
    resetModal() {
      this.newAlias = ''
      this.newAliasState = null
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.addAlias()
    },
    addAlias() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return
      }
      // Push the name to submitted names
      // this.submittedNames.push(this.newAlias)
      // Hide the modal manually
      this.$nextTick(() => {
        this.$bvModal.hide('add-alias-modal')
      })

      this.$store.dispatch('createAlias', {refCompany: this.newAliasCompany, aname: this.newAlias})
    },
    removeAlias(alias) {
      this.$store.dispatch('deleteAlias', alias)
    },
  }

  vueComponentOptions.watch = {
    contacts(value) {
      tableStore.commit('setItems', value)
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

const store = tableTools.TableStore('ContactsTable')

export default vueComponentFn(store)
