<template>
  <dropdown ref="dropdown">
    <btn class="dropdown-toggle calendar-btn">{{ date }} <i class="fa fa-calendar"></i></btn>
    <template slot="dropdown">
      <li><date-picker v-model="date"/></li>
    </template>
  </dropdown>
</template>

<script>
import {memoizedLookupFunction} from '@/helpers/functions'

var vueComponentOptions = {}

var vueComponentFn = function(store) {
  vueComponentOptions.props = {
    filterKey: {
      type: String,
      required: true,
    },
    compare: {
      type: Function,
      required: true,
    },
  }

  vueComponentOptions.data = function() {
    return {
      date: null,
    }
  }

  vueComponentOptions.watch = {
    date(value) {
      var self = this
      var dateValue = new Date(value)

      if (value !== null && value !== '') {
        var comparatorFn = (function(fValue) {
          var derefFuncs = {}
          return function(item) {
            return self.compare(memoizedLookupFunction(item, self.filterKey, derefFuncs), fValue)
          }
        })(dateValue)

        const payload = {
          key: self.filterKey,
          comparator: comparatorFn,
        }
        store.mutations.addFilter(payload)
      } else {
        store.mutations.removeFilter(self.filterKey)
      }
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn
</script>
