import fxService from '@/services/fxService'

const INITIAL_STATE = {
  currentFxRates: [],
  autoFxRates: [],
}

var state = {...INITIAL_STATE}

var getters = {
  getCurrentFxRates(state) {
    return state.currentFxRates
  },
  getAutoFxRates(state) {
    return state.autoFxRates
  },
}

const mutations = {
  setCurrentFxRates(state, payload) {
    state.currentFxRates = payload
  },
  setAutoFxRates(state, payload) {
    state.autoFxRates = payload
  },
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  resetFxRates({ state }) {
    Object.assign(state, INITIAL_STATE)
  },
  // FX Rates
  loadCurrentFxRates({ commit, state }, payload) {
    return fxService.getCurrentFxRates(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setCurrentFxRates'))
  },
  removeFxRate({ commit, getters }, payload) {
    return fxService.removeFxRate(payload)
  },
  addFxRate({ commit, state }, payload) {
    return fxService.addFxRate(payload, state.selectedCompany)
  },
  editFxRate({ commit, state }, payload) {
    return fxService.editFxRate(payload)
  },
  setFxRate({ commit, state }, payload) {
    const newValue = payload.rate
    const fxRate = payload.record.fxRate
    var data = {}

    if (fxRate.value && fxRate.manualId) {
      // edit
      data.id = fxRate.manualId
      data.manual = {rate: newValue}

      return fxService.editFxRate(data)
    } else {
      // create
      data = {
        currencyId: payload.record.docCurrencyId,
        recordType: fxRate.typeId,
        recordDate: fxRate.date,
        recordRate: newValue,
      }

      return fxService.addFxRate(data, state.selectedCompany)
    }
  },
  // Auto FX Rates
  loadCSAutoFxRates({ commit, state }) {
    return fxService.getCSAutoFxRates(state.selectedCompany)
      .then(thenHandler(commit, 'setAutoFxRates'))
  },
  activateAutoFxRate({ commit, state }, payload) {
    return fxService.activateAutoFxRate(payload, state.selectedCompany)
  },
  deactivateAutoFxRate({ commit, state }, payload) {
    return fxService.deactivateAutoFxRate(payload)
  },
  switchAutoFxRate({ commit, state }, payload) {
    return fxService.switchAutoFxRate(payload)
  },
  massDeactivateAutoFxRate({ commit, state }, payload) {
    return fxService.massDeactivateAutoFxRate(payload)
  },
}

export default {
  state,
  getters,
  mutations,
  actions,
}
