<template>
  <select class="form-control form-control-sm" @change="setFilter">
      <option value=""></option>
      <option v-for="option in options" :value="option" :key="option">{{ option }}</option>
  </select>
</template>

<script>
import {memoizedLookupFunction} from '@/helpers/functions'

function getUniques(items, path) {
  var uniques = []
  var derefFuncs = {}

  items.forEach(function(obj) {
    var value = memoizedLookupFunction(obj, path, derefFuncs)
    if (value !== null && value !== '' && uniques.indexOf(value) === -1) {
      uniques.push(value)
    }
  })
  return uniques
}

var vueComponentOptions = {}

var vueComponentFn = function(store) {
  vueComponentOptions.props = {
    items: {
      required: true,
    },
    filterKey: {
      required: true,
    },
    compare: {
      type: Function,
      required: true,
    },
  }

  vueComponentOptions.computed = {
    options() {
      return getUniques(this.items, this.filterKey)
    },
  }

  vueComponentOptions.methods = {
    setFilter(event) {
      var self = this
      var value = event.target.value

      if (value !== '') {
        var comparatorFn = (function(fValue) {
          var derefFuncs = {}
          return function(item) {
            return self.compare(memoizedLookupFunction(item, self.filterKey, derefFuncs), fValue)
          }
        })(value)

        var payload = {
          key: self.filterKey,
          comparator: comparatorFn,
        }
        store.commit('addFilter', payload)
      } else {
        store.commit('removeFilter', self.filterKey)
      }
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn
</script>
